.root {
  width: 100%;

  .input {
    color: #fff;
    font-size: 14px;
    padding: 0;

    &.small {
      font-size: 12px;
      line-height: 14px;
    }
  }

  .inputLabel {
    color: #a2a7b9;
  }

  .inputWrapper {
    font-size: 14px;
    border-radius: 0;
    border-bottom: 1px solid transparent;
    background-color: transparent;
    color: rgba(255, 255, 255, 0.5);
    padding: 6px 0 7px;

    &:hover {
      border-color: #0078ff;
      background-color: transparent;
    }

    &.focused:not(input) {
      border-color: #0078ff;
    }

    &.adornedEnd {
      padding-right: 9px;
    }

    &.adornedStart {
      padding-left: 9px;
    }

    &.error {
      color: #f96c5a;
      // border-color: #f96c5a !important;

      .input {
        color: #f96c5a;
      }
    }
  }
}
