@font-face {
  font-family: Inter;
  font-weight: 400;
  font-style: normal;
  src: url("./fonts/Inter/Inter-Regular.woff") format("woff");
}

@font-face {
  font-family: Inter;
  font-weight: 500;
  font-style: normal;
  src: url("./fonts/Inter/Inter-Medium.woff") format("woff");
}

@font-face {
  font-family: Inter;
  font-weight: 600;
  font-style: normal;
  src: url("./fonts/Inter/Inter-SemiBold.woff") format("woff");
}

@font-face {
  font-family: Inter;
  font-weight: 700;
  font-style: normal;
  src: url("./fonts/Inter/Inter-Bold.woff") format("woff");
}

html {
  height: 100%;
  background-color: #161617;
}

body {
  height: 100%;
  margin: 0;
  font-family: "Inter", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans",
    "Droid Sans", "Helvetica Neue", sans-serif;
}

#root {
  min-height: 100%;
  height: 100%;
  box-sizing: border-box;
}

.app {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;

  .form {
    width: 300px;
  }
}
